<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .contenido_curso
    .mn.py-0 
      .titulo-principal.mb-5
        .titulo-principal__numero
          span 2
        h1 Concepto y definiciones de las categorías:  sexo, género, Orientación sexual, Identidad de género, intersexualidad en el marco de la Política Púbica, Decreto 762 de 2018 y la Resolución 2138 de 2023, por la cual se adoptan los lineamientos de transversalización del enfoque de género

    .mn.bg-tema-2v2
      .row.justify-content-center.mb-5.align-items-center
        .col-lg-4.mb-lg-0.mb-3(data-aos="fade-right"): img(src='@/assets/curso/tema2/1v2.png', alt='')
        .col-lg-8(data-aos="fade-left")
          p.mb-3 En el marco de la atención en salud de las personas que componen los sectores LGBTIQ+ y de personas con orientaciones sexuales e identidades de género no hegemónicas, se destacan aquellas normas que han buscado garantizar el ejercicio pleno de los derechos. 
          .tarjeta(style="background-color: #423755 ").p-4
            .row.justify-content-center.align-items-center
              .col-lg-1.col-4.mb-lg-0.mb-3: img(src='@/assets/curso/tema2/2.svg', alt='')
              .col-lg-11
                p.mb-0.text-white El Decreto 762 de 2018, adopta la Política Pública para la garantía del ejercicio efectivo de los derechos de las personas que componen los sectores LGBTIQ+. y con orientaciones sexuales e identidades de género no hegemónicas.   


      p.mb-5 Entender la diversidad humana comienza por diferenciar entre el sexo biológico y la identidad de género. #[span.txt-color-primario #[strong A continuación, se explican estos términos clave:]]

      .row.mb-5.justify-content-center
        .col-lg-5
          .tarjeta-bg.color-acento-botones.p-4.h-100
              .tarjeta-bg__contenido.bg-white.shadow.tarjeta.h-100
                img(src='@/assets/curso/tema2/40.svg', alt='')
                .p-4.text-center
                  h5 Sexo (biológico o asignado al nacer) 
                  p Desde la perspectiva biológica, el sexo se refiere a las características morfológicas del cuerpo. Las categorías utilizadas para estas características en los seres humanos son hombre, mujer e intersexual (SIVIGE, 2016). La intersexualidad es una variación orgánica bajo la cual el desarrollo del sexo cromosómico, gonadal o anatómico no coincide con los dos sexos que tradicionalmente se asigna, genéticas, endocrinas.

        .col-lg-5
          .tarjeta-bg.color-acento-botones.p-4.h-100
            .tarjeta-bg__contenido.bg-white.shadow.tarjeta.h-100
              img(src='@/assets/curso/tema2/41.svg', alt='')
              .p-4.text-center
                h5 Identidad de género
                p Hace referencia a la vivencia individual y personal del género. Es independiente del sexo asignado al nacer. Incluye la vivencia personal del cuerpo, que puede o no involucrar transformaciones corporales escogidas libremente. Incluye también otras expresiones de género, tales como la forma de vestir, el modo de hablar y la expresión corporal. (Decreto 1066 de 2015).


      .titulo-tres.mb-5
        h5.mb-0 Clasificación de la identidad de género.  

      SlyderF(columnas="col-lg-6 col-xl-4").mb-5
        .tarjeta-bg.color-acento-botones.p-4.h-100
            .tarjeta-bg__franja
            .tarjeta-bg__contenido.bg-white.shadow.tarjeta.h-100
              img(src='@/assets/curso/tema2/42b.svg', alt='')
              .p-4.text-center
                h5.txt-color-primario Masculina
                p Es una identidad conformada por un conjunto de características inscritas en lo que la expectativa colectiva ha construido social, histórica y culturalmente respecto a los hombres (SIVIGE, 2016).


        .tarjeta-bg.color-acento-botones.p-4.h-100
          .tarjeta-bg__franja
          .tarjeta-bg__contenido.bg-white.shadow.tarjeta.h-100
            img(src='@/assets/curso/tema2/43.svg', alt='')
            .p-4.text-center
              h5.txt-color-primario Femenina
              p Es la identidad conformada por un conjunto de características inscritas en lo que la expectativa colectiva  ha construido social, histórica y culturalmente respecto a las mujeres (SIVIGE, 2016).

      
        .tarjeta-bg.color-acento-botones.p-4.h-100
          .tarjeta-bg__franja
          .tarjeta-bg__contenido.bg-white.shadow.tarjeta.h-100
            img(src='@/assets/curso/tema2/44b.svg', alt='')
            .p-4.text-center
              h5.txt-color-primario Cisgénero
              p Una persona cisgénero es aquella que se identifica con el sexo que le fue asignado al nacer y con las características de género socioculturalmente asignadas a ese sexo. Es decir, es una persona que acepta la correspondencia que existe entre su sexo y su género. 

      
        .tarjeta-bg.color-acento-botones.p-4.h-100
          .tarjeta-bg__franja
          .tarjeta-bg__contenido.bg-white.shadow.tarjeta.h-100
            img(src='@/assets/curso/tema2/45.png', alt='')
            .p-4.text-center
              h5.txt-color-primario Transgénero
              p Es la identidad en la que el sexo biológico o asignado al nacer no coincide con las normas y expectativas sociales tradicionalmente asociadas a ese sexo o con las características inscritas en lo que social, histórica y culturalmente se ha construido como femenino o masculino (SIVIGE, 2016). 

      
        .tarjeta-bg.color-acento-botones.p-4.h-100
          .tarjeta-bg__franja
          .tarjeta-bg__contenido.bg-white.shadow.tarjeta.h-100
            img(src='@/assets/curso/tema2/46.png', alt='')
            .p-4.text-center
              h5.txt-color-primario Otra
              p Una persona trans puede construir su identidad de género independientemente de intervenciones quirúrgicas o tratamientos médicos. Para todas aquellas personas que no se identifiquen con ninguna de las identidades de género anteriormente expuestas.



      figure.mb-5
        img(src='@/assets/curso/tema2/48.png', style="max-width: 990px").mx-auto


    h2 Orientación sexual

    p.mb-5 Se trata de la atracción sexual, afectiva y/o erótica que una persona siente hacia otras y a la capacidad de mantener relaciones afectivas y sexuales con esas personas.

    //h3.txt-color-primario.mb-5 Clasificación de la Orientación Sexual 

    .titulo-tres.mb-5
      h5.mb-0 Clasificación de la Orientación Sexual 

    .mn.mb-5.bg-tema-4v2.py-0    

      .row.justify-content-center.mb-5
        .col-lg-9.position-relative 
          .avatar
            figure
              img(src="@/assets/curso/tema2/54b.svg", alt="alt").img250
          .crd.crd--avatarHorizontal2.crd--first2.py-4
            .row.align-items-center
              .col-auto
                figure
                  img(src="@/assets/curso/tema2/49.svg", alt="alt").img100
              .col
                p #[strong Heterosexual:] persona cuya atracción afectiva, erótica o sexual se dirige hacia personas de sexo o identidad de género distinto al suyo.
          .crd.crd--avatarHorizontal2.crd--avatarHorizontal2--c2.py-4
            .row.align-items-center
              .col-auto
                figure
                  img(src="@/assets/curso/tema2/50.svg", alt="alt").img100
              .col
                p #[strong Homosexual:] persona cuya atracción afectiva, erótica o sexual se dirige hacia personas del mismo sexo o la misma identidad de género como en el caso de las mujeres que sienten atracción afectiva, erótica o sexual hacia otras mujeres (lesbianas - gay) o de los hombres que sienten atracción afectiva, erótica o sexual hacia otros hombres (gay).
          .crd.crd--avatarHorizontal2.crd--avatarHorizontal2--c3.py-4
            .row.align-items-center
              .col-auto
                figure
                  img(src="@/assets/curso/tema2/51.svg", alt="alt").img100
              .col
                p #[strong Bisexual:] persona cuya atracción afectiva, erótica o sexual se dirige o materializa con personas de ambos sexos o identidades de género.
          .crd.crd--avatarHorizontal2.crd--avatarHorizontal2--c4.py-4
            .row.align-items-center
              .col-auto
                figure
                  img(src="@/assets/curso/tema2/52.svg", alt="alt").img100
              .col
                p #[strong Asexual:] persona que no experimenta atracción sexual por otras personas o la experimenta ocasionalmente o solo bajo ciertas circunstancias. Las personas asexuales pueden relacionarse afectiva y románticamente, y su orientación sexual no implica necesariamente que no se practique sexo que no se pueda sentir excitación sexual (Morris, 2021).
          .crd.crd--avatarHorizontal2.crd--last2.crd--avatarHorizontal2--c5.py-4
            .row.align-items-center
              .col-auto
                figure
                  img(src="@/assets/curso/tema2/53.svg", alt="alt").img100
              .col
                p #[strong Pansexual:] persona que siente atracción romántica y/o sexual hacia otras personas independientemente de su identidad de género o sexo.

      br 
      figure.mb-5
        img(src="@/assets/curso/tema2/55.png", style="max-width: 992px").mx-auto


      .row.mb-5.justify-content-center
        .col-lg-5
          .tarjeta-bg.color-acento-botones.p-4.h-100
              .tarjeta-bg__contenido.bg-white.shadow.tarjeta.h-100
                img(src='@/assets/curso/tema2/40c.svg', alt='')
                .p-4.text-center
                  h5 Género
                  p Se refiere a los roles, las características y oportunidades definidos por la sociedad y se consideran apropiados para los hombres, las mujeres, los niños, las niñas y las personas con identidades no binarias. El género es también producto de las relaciones entre las personas y puede reflejar la distribución de poder entre ellas. No es un concepto estático, sino que cambia con el tiempo y del lugar.

        .col-lg-5
          .tarjeta-bg.color-acento-botones.p-4.h-100
            .tarjeta-bg__contenido.bg-white.shadow.tarjeta.h-100
              img(src='@/assets/curso/tema2/41c.svg', alt='')
              .p-4.text-center
                h5 Expresiones de género
                p La expresión de género es la manifestación externa del género de una persona, a través de su aspecto físico, la cual puede incluir el modo de vestir, el peinado o la utilización de artículos cosméticos, o a través de manierismos, de la forma de hablar, de patrones de comportamiento personal, de comportamiento o interacción social, de nombres o referencias personales, entre otros. La expresión de género de una persona puede o no corresponder con su identidad de género auto-percibida. (Sentencia C-356/19).


      .titulo-tres.mb-5
        h5.mb-0 Otras definiciones a tener en cuenta:  

    .row.align-items-stretch.mb-5
      .col-lg-5
        figure
          img(src="@/assets/curso/tema2/57b.png").mx-auto
      .col-lg-7
        .tarjeta-slyder.tarjeta.px-5.py-4.h-100
          SlyderA(tipo="b")
            div
              h4(style="color: #0F5FFE").mb-4 Transmasculinas
              p Personas asignadas con el género femenino al nacer, por sus genitales. Pero que se identifica con lo masculino. 

            div
              h4(style="color: #0F5FFE").mb-4 No hegemónicas
              p Ausencia de dominio de una entidad sobre otras de cualquier tipo.

            div
              h4(style="color: #0F5FFE").mb-4 Sistema binario del género/sexo
              p Explicación predominante en la cultura occidental en la cual se le da al género y al sexo dos categorías: masculino/hombre y femenino/mujer, excluyendo otras opciones. “Tal sistema o modelo es excluyente puesto que no tienen en cuenta las categorías de las personas trans o intersex”. 

            div
              h4(style="color: #0F5FFE").mb-4 Persona transexual
              p Las personas transexuales se sienten y se perciben a sí mismas como pertenecientes al género opuesto al que social y culturalmente se le asigna a su sexo biológico y toman como opción una intervención médica, hormonal, quirúrgica o ambas con el fin de para adecuar su apariencia física–biológica respecto a su realidad psíquica, espiritual y social.

            div
              h4(style="color: #0F5FFE").mb-4 Persona travesti
              p Persona que manifiesta una expresión de género ya sea de manera permanente o transitoria por medio del uso de prendas de vestir y actitudes del género opuesto al sexo asignado al nacer.

            div
              h4(style="color: #0F5FFE").mb-4 Salud con equidad
              p Ausencia de diferencias evitables, injustas o remediables entre grupos de personas debido a sus circunstancias sociales, económicas, demográficas o geográficas (OPS). 

              div
              h4(style="color: #0F5FFE").mb-4 Atención humanizada
              p Humanizar es un asunto ético, que tiene que ver con los valores que conducen la conducta en el ámbito de la salud proporcionando cuidados a la persona de manera solidaria, digna, con respeto y empatía. 

            div
              h4(style="color: #0F5FFE").mb-4 Enfoque diferencial
              p En el campo de la Salud el enfoque diferencial, además de la protección de los sujetos colectivos de derechos, se implementará en la intervención y respuesta del sector salud frente a condiciones y situaciones generadoras de vulneraciones y dinámicas de vulnerabilidad que estructuran discriminación, segregación y exclusión en individuos y colectivos. (Resolución 2367 de 2023, Ministerio de Salud y Protección Social). 

            div
              h4(style="color: #0F5FFE").mb-4 Enfoque de género
              p Establece consideraciones específicas en materia de inequidad, para el acceso a la salud, la participación, la garantía y las oportunidades en salud a razón de la construcción social del sexo, el género y la orientación sexual en una sociedad, las cuales establecen diferenciales que deben ser protegidos, buscando una igualdad material y simbólica que supere todas las formas de violencia, discriminación, segregación y cualquier manifestación de desigualdad para la atención sanitaria por parte del Estado. (Ministerio de Salud y Protección Social. Oficina de Promoción Social, 2023). 


    p.mb-5 Antes de abordar las directrices de la presente política pública, es crucial comprender el significado del acrónimo LGBTIQ+, que se define como una categoría identitaria y política. Esta categoría engloba diversas orientaciones sexuales, identidades de género y diferentes estados de intersexualidad. El acrónimo LGBTIQ+ se centra en la reivindicación y exigibilidad de los derechos en el ámbito de las luchas sexuales y de género:

    .mn.bg-4.mb-5
      .row.justify-content-center.mb-5
        .col-lg-5.col-5.mb-lg-0.mb-3: img(src='@/assets/curso/tema2/22.svg', alt='')
        .col-lg-7
          .row.justify-content-center.mb-5
            .col-lg-1.col-2.mb-lg-0.mb-3
              img(src='@/assets/curso/tema2/23.svg', alt='') 
                
            .col-lg-11.col-20
              p #[span(style='color: #dd1948').fw-bold Lesbiana:] mujer que se siente atraída sexual y afectivamente por otras mujeres. 
          .row.justify-content-center.mb-5
            .col-lg-1.col-2.mb-lg-0.mb-3
              img(src='@/assets/curso/tema2/24.svg', alt='') 
                
            .col-lg-11.col-20
              p #[span(style='color: #e95800').fw-bold Gay:] hombre que se siente atraído sexual y afectivamente por otros hombres.  
          .row.justify-content-center.mb-5
            .col-lg-1.col-2.mb-lg-0.mb-3
              img(src='@/assets/curso/tema2/25.svg', alt='') 
                
            .col-lg-11.col-20
              p #[span(style='color: #ffb000').fw-bold Trans:] corresponde con la identidad de género que no corresponde con aquellas asignadas al nacer. Dentro de la clasificación trans se encuentra la denominación de transgénero, quien es la persona que nace con ciertas características físicas que no tienen correspondencia con el género con el cual se identifica.  La persona transexual es quien ha comenzado un tratamiento hormonal o de nivel quirúrgico para realizar cambio de sexo.   
          .row.justify-content-center.mb-5
            .col-lg-1.col-2.mb-lg-0.mb-3
              img(src='@/assets/curso/tema2/26.svg', alt='') 
                
            .col-lg-11.col-20
              p #[span(style='color: #3d9924').fw-bold Bisexual:] persona que siente atraída sexual y afectivamente hacia hombres y mujeres indistintamente. 
          .row.justify-content-center.mb-5
            .col-lg-1.col-2.mb-lg-0.mb-3
              img(src='@/assets/curso/tema2/27.svg', alt='') 
                
            .col-lg-11.col-20
              p #[span(style='color: #009ee0').fw-bold Intersexual:] persona que nace con características físicas de ambos géneros.  
          .row.justify-content-center.mb-5
            .col-lg-1.col-2.mb-lg-0.mb-3
              img(src='@/assets/curso/tema2/28.svg', alt='') 
                
            .col-lg-11.col-20
              p #[span(style='color: #853c89').fw-bold Queer' o sin etiquetas:]  personas que no se categorizan con las etiquetas tradicionales o aceptadas socialmente.  
          .row.justify-content-center.mb-5
            .col-lg-1.col-2.mb-lg-0.mb-3
              img(src='@/assets/curso/tema2/29.svg', alt='') 
                
            .col-lg-11.col-20
              p #[span(style='color: #12263f').fw-bold Otras:] personas que no se sienten identificadas ni como lesbiana, gay, trans, bisexual o intersexual.
    .titulo-tres.mb-5
      h5.mb-0 Dentro del acrónimo tenemos identidades y orientaciones  
    p.mb-5(data-aos='fade-right') La T, la Q y el + son identidades:

    img(src='@/assets/curso/tema2/30b.png', alt='').mb-4

    .mn.bg-5.mb-5
      p.mb-5(data-aos='fade-right') La L, la G, la B Son orientaciones sexuales:
      .row.justify-content-center.mb-5
        .col-lg-3.mb-lg-0.mb-3
