        .col-lg-3.mb-lg-0.mb-3
          .tarjeta.overflow-hidden.bg-white.shadow.h-100
            img(src='@/assets/curso/tema2/34b.png', alt='')
            .p-4.text-center
              h5 Intersexualidad
              p Es una variación biológica donde el desarrollo sexual no encaja en las categorías masculina o femenina.
      
      
      .tarjeta(style="background: #423755").p-3.px-4.mb-5
        .row.align-items-center
          .col-auto
            img(src='@/assets/curso/tema2/58.svg', alt='', style="max-width: 64px").mx-auto
          .col
            p.text-white.mb-0 Es importante respetar siempre los pronombres de las personas. Si no sabes bien qué palabras utilizar para describirlas, pregúntales con respeto.


      p.mb-5(data-aos='fade-right') En la actualidad, diversos conceptos y prácticas sociales han adquirido relevancia por su impacto en la sociedad. Entre estos se encuentran:
      .row.justify-content-center.mb-3
        .col-lg-6.mb-lg-0.mb-3
          .row.justify-content-center.align-items-center
            .col-auto: img.d-inline-flex(src='@/assets/curso/tema2/35.svg', style="max-width: 85px").
            .col
              h5(style="color: #0F5FFE") Mito:
              p.mb-0 Historia imaginaria que altera las verdaderas cualidades de una persona o cosa, dándoles más o menos valor del que realmente tienen.
          hr
        .col-lg-6.mb-lg-0.mb-3
          .row.justify-content-center.align-items-center
            .col-auto: img.d-inline-flex(src='@/assets/curso/tema2/36.svg', style="max-width: 85px").
            .col
              h5(style="color: #0F5FFE") Discriminación:
              p.mb-0 Trato diferente y perjudicial hacia una persona por motivos de raza, sexo, ideas políticas, religión, entre otros.
          hr
      .row.justify-content-center.mb-3
        .col-lg-6.mb-lg-0.mb-3
          .row.justify-content-center.align-items-center
