              p.mb-0 Proporcionar información insuficiente u omitirla.
          hr
        .col-lg-6.mb-lg-0.mb-3
          .row.justify-content-center.align-items-center
            .col-auto: img.d-inline-flex(src='@/assets/curso/tema2/38.svg', style="max-width: 85px").
            .col
              h5(style="color: #0F5FFE") Omisión:
              p.mb-0 No realizar algo de manera voluntaria o involuntaria.
          hr
      .row.justify-content-center.mb-3
        .col-lg-12.mb-lg-0.mb-3
          .row.justify-content-center.align-items-center
            .col-auto: img.d-inline-flex(src='@/assets/curso/tema2/39.svg', alt='', style="max-width: 85px").
            .col
              h5(style="color: #0F5FFE") Patologización
              p.mb-0 Clasificar un fenómeno normal como enfermedad. En el contexto de adultos, niñas, niños y adolescentes LGBTIQ+, se refiere a etiquetarlos como enfermos en función de su orientación sexual, identidad de género o expresión de género. Esta práctica ha sido y es una de las principales causas de violación de los derechos humanos de la población LGBTIQ+.
          
    p.mb-5(data-aos='fade-right') Para practicar lo aprendido, lo invitamos a sumergirse en las siguientes actividades de aprendizaje:  


